import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import axios from 'axios';
import styles from './VerifyEmail.module.css';

const VerifyEmail = () => {
  const [verificationStatus, setVerificationStatus] = useState('loading');
  const [message, setMessage] = useState('');
  const location = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    const verifyEmail = async () => {
      try {
        const searchParams = new URLSearchParams(location.search);
        const token = searchParams.get('token');

        if (!token) {
          setVerificationStatus('error');
          setMessage('Токен подтверждения не найден');
          return;
        }

        const response = await axios.get(`/api/auth/verify-email?token=${token}`);
        setVerificationStatus('success');
        setMessage(response.data.message);
      } catch (error) {
        setVerificationStatus('error');
        setMessage(error.response?.data?.message || 'Произошла ошибка при подтверждении email');
      }
    };

    verifyEmail();
  }, [location.search]);

  return (
    <div className={styles.container}>
      <div className={styles.content}>
        <h1 className={styles.title}>Подтверждение Email</h1>
        {verificationStatus === 'loading' && (
          <div className={styles.status}>
            <p className={`${styles.message} ${styles.loading}`}>Проверка вашего email...</p>
          </div>
        )}
        {verificationStatus === 'success' && (
          <div className={styles.status}>
            <p className={`${styles.message} ${styles.success}`}>{message}</p>
            <button onClick={() => navigate('/login')} className={styles.loginButton}>
              Перейти к входу
            </button>
          </div>
        )}
        {verificationStatus === 'error' && (
          <div className={styles.status}>
            <p className={`${styles.message} ${styles.error}`}>{message}</p>
            <button onClick={() => navigate('/')} className={styles.homeButton}>
              Вернуться на главную
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default VerifyEmail;